<template>
  <div class="chart">
    <highcharts
      :constructorType="'stockChart'"
      class="hc"
      :options="chartOptions"
      ref="chart"
    ></highcharts>
  </div>
</template>

<script>
export default {
  props: {
    sensor: Object,
  },
  methods: {
    formatData() {
      let date;
      var list = new Array();
      for (let i in this.sensor.values) {
        date = new Date(this.sensor.values[i]["date_created"]).getTime();
        list[i] = [date, this.sensor.values[i]["value"]];
      }
      return list;
    },
  },
  data() {
    return {
      chartOptions: {
        chart: {
          type: "spline",
          events: {},
        },
        rangeSelector: {
          buttons: [
            {
              type: "day",
              count: 1,
              text: "1D",
            },
            {
              type: "all",
              count: 1,
              text: "All",
            },
          ],
          selected: 1,
          inputEnabled: false,
        },
        navigator: {
          enabled: false,
        },
        scrollbar: {
          enabled: false,
        },
        title: {
          text: "",
        },
        xAxis: {
          type: "datetime",
        },
        yAxis: {
          title: {
            text: this.sensor.type,
          },
          opposite: false,
        },
        plotOptions: {
          spline: {
            dataLabels: {
              enabled: false,
            },
            enableMouseTracking: true,
          },
        },
        credits: {
          enabled: false,
        },
        series: [
          {
            name: this.sensor.type,
            data: this.formatData(),
            dataGrouping: {
              enabled: false,
            },
          },
        ],
        exporting: {
          buttons: {
            contextButton: {
              menuItems: [
                "printChart",
                "downloadPNG",
                "downloadJPEG",
                "downloadPDF",
                "downloadSVG",
              ],
            },
          },
        },
      },
    };
  },
};
</script>

<style lang="scss" scoped>
.chart {
  margin: auto;
  width: 60%;
}
</style>